



















import Vue from 'vue'
import {mapActions, mapGetters} from 'vuex'

export default Vue.extend({
  name: 'Search',
  data() {
    return {
      count: 9
    }
  },
  computed: {
    ...mapGetters(['searchText']),
    isValid(): boolean {
      return this.searchText.length <= this.count
    }
  },
  methods: mapActions(['search'])
})
