























import Vue from 'vue'

export default Vue.extend({
  name: 'Header',
  computed: {
    title() {
      switch (this.$route.name) {
        case 'home':
          return 'Random users'
        case 'user':
          return 'User details'
        default:
          return 'Untitled'
      }
    },
    icon() {
      switch (this.$route.name) {
        case 'home':
          return 'mdi-refresh'
        case 'user':
          return 'mdi-arrow-left'
        default:
          return 'Untitled'
      }
    },
    isBackBtn() {
      switch (this.$route.name) {
        case 'home':
          return false
        case 'user':
          return true
        default:
          return false
      }
    }
  }
})
